import React, {useState} from 'react'
import {auth} from '../firebase'
import {Button, Input, Modal} from 'antd'
import {toast} from 'react-toastify'

const ForgetPassword = ({passwordModal, setPasswordModal})=>{
    const [email, setEmail] = useState('')

    const handleSubmit = async()=>{
        const config = {
            url:`${process.env.REACT_APP_FORGOT_PASS_REDIRECT_URL}/login`,
            handleCodeInApp:true
        }
       await auth.sendPasswordResetEmail(email, config).then((res)=>{
           toast.success(`Confirmation Link has been sent to ${email}`)
           setPasswordModal(false)
       }).catch((err)=>{
           toast.error('Sorry...we were unable to send the confirmation link!!')
       })
    }
    
    return(
        <>
        <Modal footer={null} visible={passwordModal} onCancel={()=>setPasswordModal(false)}>
            <p className="forgot-pass-head">Forgot Your Password ?</p>
            <p className="forget-pass-sub">Please provide your email for reseting your password.</p>
            <input type="text" value={email} onChange={(e)=>setEmail(e.target.value)} className="form-control mt-4" placeholder="Enter Your Email"/>
            <Button disabled={!email.includes('@')} style={{height:'40px',color:'white', textTransform:'uppercase', letterSpacing:'1px' ,backgroundColor:'#027782'}} shape="round" block className="mt-4" onClick={handleSubmit}>Submit</Button>
        </Modal>
        </>
    )
}

export default ForgetPassword