import React, {useState, useEffect} from 'react'
import AdminNav from '../../components/nav/AdminNav'
import {useSelector} from 'react-redux'
import { Button, Input, Select, Tooltip, Modal, Card } from 'antd'
import { listCategories } from '../../functions/category'
import { createSub, listSubs, removeSub } from '../../functions/sub'
import {DeleteOutlined, EditOutlined, PlusCircleOutlined, ReloadOutlined} from '@ant-design/icons'
import {Link} from 'react-router-dom'
import { toast } from 'react-toastify'

const Sub = ()=>{
    const [categories, setCategories] = useState([])
    const [isModalVisible, setIsModalVisible] = useState(false)
    const [name, setName] = useState('')
    const [category, setCategory] = useState('Choose The Category')
    const [subs, setSubs] = useState('')
    const {user} = useSelector((state)=>({...state}))
    const {Option} = Select

    useEffect(()=>{
        loadAllCategories();
        loadAllSubs();
    }, [])

    const loadAllCategories = ()=>{
        listCategories().then((res)=>{
            setCategories(res.data)
        }).catch((err)=>{
            console.log(err)
        })
    }

    const loadAllSubs = ()=>{
        listSubs().then((res)=>{
            setSubs(res.data)
        }).catch((err)=>{
            console.log(err)
        })
    }

    const handleSubmit = ()=>{
        createSub(user.token, {name, category}).then((res)=>{
            setName('')
            loadAllSubs()
            setCategory('Choose The Category')
            toast.success(`${name} is successfully created!!`)
        }).catch((err)=>{
            toast.error(`${name} was not created...Try Again!!`)
        })
    }
    const handleRemove = (slug)=>{
        removeSub(user.token, slug).then((res)=>{
            toast.success(res.data.success)
            loadAllSubs()
        }).catch((err)=>{
            toast.error(err.message)
        })
    }
    return(
        <>
            <div className="container-fluid">
            <div className="row">
                <AdminNav/>
                <div className="col-10 pl-2 pr-1 py-5 bg-gray  dash-content">
                    <Card className="card_table">
                    <div className="table-heading d-flex align-items-center justify-content-between">
                        <p className="pt-3 pl-3 text-white">SUB CATEGORIES</p>
                        <div className=" d-flex  align-items-center justify-content-end">
                        <Tooltip title='Create New Product'><PlusCircleOutlined className=" mr-2 plus_icon" onClick={()=>setIsModalVisible(true)}/></Tooltip>
                        <Tooltip title="Refresh"><ReloadOutlined className="mr-2 plus_icon" onClick={()=>loadAllSubs()}/></Tooltip>
                        </div>
                    </div>
                    
                    <table className="table table-responsive-sm">
                        <thead className="thead-table">
                            <tr>
                                <th scope="col">Date</th>
                                <th scope="col">Title</th>
                                <th scope="col">Category</th>
                                <th scope="col">Actions</th>
                            </tr>
                        </thead>
                        <tbody>
                            {subs && subs.map((item)=>{
                                let {createdAt,name, slug, category} = item
                                return(
                                    <>
                                        <tr className="table-body-row">
                                            <td>{createdAt.split('T')[0]}</td>
                                            <td >{name}</td>
                                            <td>{category.name}</td>
                                            <td><Tooltip title={'Delete'}><DeleteOutlined className="text-danger pr-2" onClick={()=>handleRemove(slug)}/></Tooltip> 
                                                <Tooltip title={"Edit"}><Link to={`/admin/update/sub/${slug}`}><EditOutlined className="text-warning"/></Link></Tooltip></td>
                                        </tr>
                                    </>
                                )
                            })}
                        </tbody>
                    </table>
                    </Card>
                </div>
                
                <Modal title="Create Sub" visible={isModalVisible} centered onCancel={()=>setIsModalVisible(false)} footer={null}>
                    <Select placeholder="Choose The Category" value={category} className="mb-3" onChange={(e)=>setCategory(e)}>
                        {categories && categories.map((item)=>{
                            return(
                                <>
                                    <Option key={item.slug} value={item._id}>{item.name}</Option>
                                </>
                            )
                        })}
                    </Select><br/>
                    <Input type="text" placeholder="Sub Name" value={name} onChange={(e)=>setName(e.target.value)}/><br/>
                    <Button type="primary" shape="round" block className=" my-3" onClick={handleSubmit}>Submit</Button>
                </Modal>
            </div>  
        </div>
        </>
    )
}

export default Sub