import React, {useEffect, useState} from 'react'
import { readOrder } from '../../functions/order'
import './OrderPlaced.css'
import {useSelector} from 'react-redux'

const OrderPlaced = ({history, match})=>{
    let {orderId} = match.params
    const [order, setOrder] = useState('')
    let {user} = useSelector((state)=>({...state}))
    useEffect(()=>{
        readOrder(user && user.token, orderId).then((res)=>{
            setOrder(res.data[0])
        }).catch((err)=>{
            console.log(err)
        })
    }, [orderId])
    return(
        <>
            <section className="order-placed py-5">
                <div className="container mx-auto">
                    <div className="order_placed_cont text-center pb-5">
                        {order ?(
                            <>
                                <div className="check_cont d-flex justify-content-center pt-5 pb-3">
                                    <i className="fa fa-check text-center text-success"></i>
                                </div>
                                <p className="success_text text-center text-success">Order Placed Successfully</p>
                                <p className="order_number_text">Your Order Number Is :- <span className="text-danger">{order && order.paymentIntent.order_id}</span></p>
                                <p className="email_text">you'll recieve an email confirmation shortly to <span className="text-success">{user && user.email}</span></p>
                                <div className="order_total d-flex justify-content-center mt-5">
                                    <p>Order Total - </p>
                                    <p className="pl-4">$ {order && order.paymentIntent.amount / 100}</p>
                                </div>
                                <div className="pay_meth d-flex justify-content-center ">
                                    <p>Payment Method - </p>
                                    <p className="pl-4">{order && order.paymentIntent.payment_method}</p>
                                </div>
                                <div className="success_buttons text-center mt-4">
                                    <button className="btn-first mr-0 mr-md-4 mb-3" onClick={()=>history.push(`/user/order/${order && orderId}`)}>View This Order</button>
                                    <button className="btn-second" onClick={()=>history.push('/shop')}>Continue Shopping</button>
                                </div>
                            </>
                        ):(
                            <>
                                <div className="delivery_loader">
                                    <img src="https://media4.giphy.com/media/cmCHuk53AiTmOwBXlw/giphy.gif" className="img-fluid"/>
                                </div>
                            </>
                        )}
                        
                    </div>
                </div>
            </section>
        </>
    )
}

export default OrderPlaced