import { Input, Modal,Button } from 'antd'
import React, {useState} from 'react'
import { auth, googleAuthProvider } from '../firebase'
import { createOrUpdateUser } from '../functions/auth'
import {useHistory} from 'react-router-dom'
import {useDispatch} from 'react-redux'
import './RegisterFormModal.css'

const RegisterFormModal = ({isModalVisible, setIsModalVisible})=>{
    const initialState = {
        firstName:'',
        lastName:'',
        phoneNumber:'',
        HouseNo:'',
        LandMark:'',
        City:'',
        Pin:'',
        State:''
    }

    const [address, setAddress] = useState(initialState)
    const [password, setPassword] = useState('')
    let {firstName, lastName, phoneNumber, HouseNo, LandMark, Pin,City, State} = address
    const history = useHistory()
    const dispatch = useDispatch()

    const handleChange = (e)=>{
        setAddress({...address, [e.target.name]:e.target.value})
    }

    const handleSubmit = async()=>{
        await auth.signInWithPopup(googleAuthProvider).then(async(res)=>{
            if(res.user.emailVerified){
                const user = auth.currentUser
                await user.updatePassword(password)
                const {token} = await user.getIdTokenResult()
                createOrUpdateUser(token, address).then((result)=>{
                    let {email, role, _id, newsletter} = result.data
                    dispatch({
                        type:'LOGGED_IN_USER',
                        payload:{
                            _id,
                            email,
                            role,
                            newsletter,
                            token
                        }
                    })
                    history.push('/')
                }).catch((err)=>{
                    console.log(err)
                })
            }
        }).catch((err)=>{
            console.log(err)
        })
    }
    return(
        <>
        <Modal className="register-form-modal" visible={isModalVisible} centered onCancel={()=>setIsModalVisible(false)} footer={null}>
            <div className="mx-auto">
                <p className="heading">Sign Up</p>
                <p className="sub-head">Please fill the form to create new account.</p>
                <div className="d-flex justify-content-between">
                    <input type="text" placeholder='First Name' value={firstName} name='firstName' onChange={handleChange} className="mr-2 form-control mt-3"/>
                    <input type="text" placeholder='Last Name' value={lastName} name='lastName' onChange={handleChange} className="form-control mt-3"/>
                </div>
                <input className="mt-3 form-control" type="password" placeholder="Password" value={password} onChange={(e)=>setPassword(e.target.value)}/>
                <input type="number" placeholder='Phone Number' value={phoneNumber} name='phoneNumber' onChange={handleChange} className="form-control mt-3"/>
                <div className="d-flex justify-content-between">
                <input type="text" placeholder='House No' value={HouseNo} name='HouseNo' onChange={handleChange} className="form-control mt-3 mr-3"/>
                <input type="text" placeholder='Land Mark' value={LandMark} name='LandMark' onChange={handleChange} className="form-control mt-3"/>
                </div>
                <div className="d-flex justify-content-between">
                <input type="text" placeholder='City' value={City} name='City' onChange={handleChange} className="mt-3 mr-3 form-control"/>
                <input type="text" placeholder='Pin' value={Pin} name='Pin' onChange={handleChange} className="mt-3 form-control"/>
                </div>
                <input type="text" placeholder='State' value={State} name='State' onChange={handleChange} className="mt-3 form-control"/>
                <Button shape="round" block className="mt-4" style={{height:'40px',color:'white', textTransform:'uppercase', letterSpacing:'1px' ,backgroundColor:'#027782'}} onClick={handleSubmit}>Submit</Button>
            </div>
        </Modal>
        </>
    )
}

export default RegisterFormModal