import Footer from '../../components/home/Footer'
import React, {useState,useEffect} from 'react'
import {useSelector} from 'react-redux'
import { getUserOrders } from '../../functions/order'
import './UserOrder.css'
import {Link} from 'react-router-dom'

const UserOrder = ({history})=>{
    const [order, setOrder] = useState([])
    const {user} = useSelector((state)=>({...state}))

    useEffect(()=>{
        loadUserOrder()
    },[user])

    const loadUserOrder = ()=>{
        getUserOrders(user && user.token).then((res)=>{
            setOrder(res.data)
        })
    }

    const showDeliveryStatus = (data)=>{
        if(data === 'Delivered'){
            return <> <i className="fa fa-circle pr-2 text-success"></i>{data}</>
        }else if(data === 'Not Proccessed'){
            return <> <i className="fa fa-circle pr-2 text-warning"></i>{data}</>
        }else if(data === 'Cancelled'){
            return <> <i className="fa fa-circle pr-2 text-danger"></i>{data}</>
        }else if(data === 'Proccessing'){
            return <> <i className="fa fa-circle pr-2 text-warning"></i>{data}</>
        }else if(data === 'Out for Delivery'){
            return <> <i className="fa fa-circle pr-2 text-warning"></i>{data}</>
        }
    }

    const showMessage = (data)=>{
        if(data === 'Delivered'){
            return 'Your Item is delivered'
        }else if(data === 'Not Proccessed'){
            return 'Your item is under Processing'
        }else if(data === 'Cancelled'){
            return 'As per your request, Order is cancelled'
        }else if(data === 'Proccessing'){
            return 'Your item is being processed'
        }else if(data === 'Out for Delivery'){
            return 'Your item is out for delivery'
        }
    }

    const handleClick = (id)=>{
       history.push(`/user/order/${id}`)
    }
    return(
        <>
            {order && order.length > 0 ? (
                <>
                <div className="container pb-5 pt-3">
                    <div className="row">
                        {order.map((data)=>{
                            return data.products.map((item)=>{
                                let {images, title, price} = item.product
                                return(
                                    <>
                                    <div className="col-11 mx-auto mt-3 order_wrap" onClick={()=>handleClick(data._id)} data-aos="fade-up" data-aos-duration="1000">
                                        <div className="row align-items-center">
                                            <div className="col-4 col-lg-3 mx-auto order_img_container">
                                                <img src={images && images[0].url}/>
                                            </div>
                                            <div className="d-none d-lg-block col-lg-3 mx-auto order-title">
                                                <p>{title}</p>
                                            </div>

                                            <div className="col-3 d-none d-lg-block mx-auto order-title">
                                                <p>{price}</p>
                                            </div>
                                            <div className="col-7 col-lg-3 mx-auto order-title text-right">
                                                <p className="d-block d-lg-none pt-1">{title}</p>
                                                <p>{showDeliveryStatus(data && data.deliveryStatus)}</p>
                                                <p>{showMessage(data && data.deliveryStatus)}</p>
                                            </div>
                                        </div>
                                    </div>
                                    </>
                                )
                            })
                        })}
                    </div>
                </div>
                </>
            ) : (
                <>
                    <section className="empty-cart my-4">
                        <div className="container py-5">
                                <img src="https://media0.giphy.com/media/L40qwPexZfBJLqSBaA/source.gif" className="img-fluid"/>
                                <h6>Order Is Empty</h6>
                                <p>You don't have any orders yet.<br/>You will find a lot of interesting products on our "Shop" page.</p>
                                <Link className="cart-btn" to="/shop">Return To Shop</Link>
                        </div>
                    </section>
                </>
            )}
            <Footer/>
        </>
    )
}

export default UserOrder