import React,{useState, useEffect} from 'react'
import { Button, Input,Modal, Card, Tooltip } from 'antd'
import AdminNav from '../../components/nav/AdminNav'
import {Link} from 'react-router-dom'
import { createCategory, listCategories, removeCategory } from '../../functions/category'
import {useSelector} from 'react-redux'
import { toast } from 'react-toastify'
import { DeleteOutlined, EditOutlined, ReloadOutlined, PlusCircleOutlined } from '@ant-design/icons'
import './Category.css'

const Category = ()=>{
    const [category, setCategory] = useState([])
    const [isModalVisible, setIsModalVisible] = useState(false)
    const [name, setName] = useState('')
    const {user} = useSelector((state)=>({...state}))
    useEffect(()=>{
        loadAllCategories()
    }, [])

    const loadAllCategories = ()=>{
        listCategories().then((res)=>{
            setCategory(res.data)
        }).catch((err)=>{
            console.log(err)
        })
    }

    const handleSubmit = ()=>{
        createCategory(user.token, name).then((res)=>{
            toast.success(`${name} has been successfully created!!`)
            loadAllCategories()
            setName('')
        }).catch((err)=>{
            toast.error(`${name} was not created...Try Again!!`)
            setName('')
        })
    }

    const handleRemove = (slug)=>{
        removeCategory(user.token, slug).then((res)=>{
            toast.success(`${slug} is successfully deleted!!`)
            loadAllCategories()
        }).catch((err)=>{
            toast.error(err.message)
        })
    }
    return(
        <>
        <div className="container-fluid">
            <div className="row">
                <AdminNav/>
                <div className="col-10 pl-2 pr-1 py-5 bg-gray  dash-content">
                    <Card className="card_table">
                    <div className="table-heading d-flex align-items-center justify-content-between">
                        <p className="pt-3 pl-3 text-white">CATEGORIES</p>
                        <div className=" d-flex  align-items-center justify-content-end">
                        <Tooltip title='Create New Product'><PlusCircleOutlined className=" mr-2 plus_icon" onClick={()=>setIsModalVisible(true)}/></Tooltip>
                        <Tooltip title="Refresh"><ReloadOutlined className="mr-2 plus_icon" onClick={()=>loadAllCategories()}/></Tooltip>
                        </div>
                    </div>
                    
                    <table className="table table-responsive-sm">
                        <thead className="thead-table">
                            <tr>
                                <th scope="col">Date</th>
                                <th scope="col">Title</th>
                                <th scope="col">Actions</th>
                            </tr>
                        </thead>
                        <tbody>
                            {category && category.map((item)=>{
                                let {createdAt,name, slug} = item
                                return(
                                    <>
                                        <tr className="table-body-row">
                                            <td>{createdAt.split('T')[0]}</td>
                                            <td >{name}</td>
                                            <td><Tooltip title={'Delete'}><DeleteOutlined className="text-danger pr-2" onClick={()=>handleRemove(slug)}/></Tooltip> 
                                                <Tooltip title={"Edit"}><Link to={`/admin/update/category/${slug}`}><EditOutlined className="text-warning"/></Link></Tooltip></td>
                                        </tr>
                                    </>
                                )
                            })}
                        </tbody>
                    </table>
                    </Card>
                </div>
                
                <Modal title="Create Category" visible={isModalVisible} centered onCancel={()=>setIsModalVisible(false)} footer={null}>
                    <Input type="text" placeholder="Category Name" value={name} onChange={(e)=>setName(e.target.value)}/><br/>
                    <Button type="primary" shape="round" block className="mt-3" onClick={handleSubmit}>Submit</Button>
                </Modal>
            </div>  
        </div>
        </>
    )
}

export default Category