import React,{useState, useEffect} from 'react'
import AdminNav from '../../components/nav/AdminNav'
import { getAllOrders, listOrderOnCriteria } from '../../functions/order'
import {useSelector} from 'react-redux'
import {Card, Tooltip, Select} from 'antd'
import {ReloadOutlined, InfoCircleOutlined} from '@ant-design/icons'
import OrderInfo from '../../components/modals/OrderInfo'
import './Dashboard.css'

const AdminDashBoard = ()=>{
    const {Option} = Select
    const [orders, setOrders] = useState('')
    const {user} = useSelector((state)=>({...state}))
    const [infoModal, setInfoModal] = useState(false)
    const [modalOrderId, setModalOrderId] = useState('')
    const [selectOp, setSelectOp] = useState('All Orders')
    const [options, setOptions] = useState(['Not Proccessed', 'Proccessing', 'Out for Delivery', 'Delivered', 'Cancelled'])
    const [loading, setLoading] = useState(false)
    useEffect(()=>{
        loadAllOrders()
        setSelectOp('All Orders')
    },[])

    const loadAllOrders = ()=>{
        setLoading(true)
        getAllOrders(user.token, -1).then((res)=>{
            setLoading(false)
            setOrders(res.data)
        }).catch((err)=>{
            setLoading(false)
        })
    }

    const handleSelectChange=(value)=>{
        setLoading(true)
        setSelectOp(value)
        if(value === 'All Orders'){
            loadAllOrders()
        }else{
            listOrderOnCriteria(user && user.token, value).then((res)=>{
                setLoading(false)
                setOrders(res.data)
            }).catch((err)=>{
                console.log(err)
                setLoading(false)
            })
        }
    }
    return(
        <>
            <div className="container-fluid">
                <div className="row">
                   <AdminNav/>
                    <div className="col-10 mx-auto bg-gray dash-content my-5">
                    <Card className="card_table">
                    <div className="table-heading d-flex align-items-center justify-content-between">
                        <p className="pt-3 pl-3 text-white">ORDERS</p>
                        <div className=" d-flex  align-items-center justify-content-end">
                        <Tooltip title="Refresh"><ReloadOutlined className="mr-2 plus_icon" onClick={()=>loadAllOrders()}/></Tooltip>
                        </div>
                    </div>
                    <table className="table table-responsive-sm table-responsive-lg">
                        {!loading && (!loading && orders.length != 0 ) && (
                            <>
                                <thead className="thead-table">
                                    <tr>
                                        <th scope="col">Order Id</th>
                                        <th scope="col">Date</th>
                                        <th scope="col">Customer</th>
                                        <th scope="col">Delivery Status</th>
                                        <th scope="col">Amount</th>
                                        <th scope="col">Payment Status</th>
                                        <th scope="col">Actions</th>
                                    </tr>
                                </thead>
                            </>
                        )}
                        <tbody>
                            {!loading ? (
                                <>
                                    {orders && orders.length > 0 ? orders.map((item)=>{
                                        let {deliveryStatus,orderedBy, paymentIntent,products, paymentStatus, createdAt, _id} = item
                                        return(
                                            <>
                                                <tr className="table-body-row">
                                                    <td >{paymentIntent.order_id}</td>
                                                    <td>{createdAt.split('T')[0]}</td>
                                                    <td >{orderedBy.email}</td>
                                                    <td>{deliveryStatus}</td>
                                                    <td>{paymentIntent.amount / 100}</td>
                                                    <td>{paymentStatus}</td>
                                                    <td><Tooltip title={'More Info'}><InfoCircleOutlined className="text-warning pr-2" onClick={()=>{setInfoModal(true); setModalOrderId(_id)}}/></Tooltip> </td>
                                                </tr>
                                            </>
                                        )
                                    }) : (
                                        <>
                                            <div className="d-flex align-items-center justify-content-center" style={{height:'300px'}}>
                                                <p className="text-center no-order">No Orders with delivery Status as {selectOp} !!</p>
                                            </div>
                                        </>
                                    )}
                                </>
                            ):(
                                <>
                                    <div className="d-flex align-items-center justify-content-center">
                                        <img style={{height:'100%'}} src="https://miro.medium.com/max/600/1*beQRWt1uWdnQM_nqCwhJnA.gif" className="img-fluid"/>
                                    </div>
                                </>
                            )}
                        </tbody>
                    </table>
                    <OrderInfo infoModal={infoModal} setInfoModal={setInfoModal} modalOrderId={modalOrderId} loadAllOrders={loadAllOrders}/>
                        <Select onChange={handleSelectChange} defaultValue={selectOp}>
                            <Option value="All Orders">All Orders</Option>
                                {options && options.map((item)=>{
                                    return(
                                        <Option value={item}>{item}</Option>
                                    )
                                })}
                        </Select>
                    </Card>
                    </div>
                </div>  
            </div>
        </>
    )
}

export default AdminDashBoard