import React, {useEffect} from 'react'
import './AdminNav.css'
import {Link} from 'react-router-dom'
import {useDispatch, useSelector} from 'react-redux'

const AdminNav = ()=>{
    const {adminNav, adminNavMob} = useSelector((state)=>({...state}))
    const dispatch = useDispatch()

    useEffect(()=>{
		let main = document.querySelectorAll('.admin_nav_lg .nav-item')
		for(let i=0; i<main.length; i++){
			main[i].classList.remove('active')
		}
		let item = document.querySelector(`.${adminNav}`)
		item.classList.add('active')
        
	}, [adminNav])

    useEffect(()=>{
		let main = document.querySelectorAll('.dash-sm .nav-item')
		for(let i=0; i<main.length; i++){
			main[i].classList.remove('active')
		}
		let item = document.querySelector(`.${adminNavMob}`)
		item.classList.add('active')
	}, [adminNavMob])

    const handleAdminNav = (e)=>{
        if(e.target.tagName == 'A'){
            let page = e.target.parentElement.className.split(' ')[1]
            let page_sm = `${page}_sm`
            dispatch({
                type:'CHANGE_ADMIN_NAV',
                payload:page
            })
            dispatch({
                type:'CHANGE_ADMIN_MOB_NAV',
                payload:page_sm
            })
        }
    }

    const handleAdminMobNav = (e)=>{
        if(e.target.tagName == 'A'){
            let page = e.target.parentElement.className.split(' ')[1]
            let page_lg = page.split('_')[0]
            dispatch({
                type:'CHANGE_ADMIN_MOB_NAV',
                payload:page
            })
            dispatch({
                type:'CHANGE_ADMIN_NAV',
                payload:page_lg
            })
        }
    }

    return(
        <>
         <div className="col-2 bg-green dash-side px-0 py-5">
            <h6 className="ml-3 text-white mb-4 d-lg-block d-none">WELCOME BIGBERRY</h6 >
            <ul className="d-flex flex-column d-lg-none navbar-nav dash-sm" onClick={handleAdminMobNav}>
                <li className="nav-item dashboard_sm text-center admin-nav-icon">
                    <Link to='/admin/dashboard' className="nav-link  "><i className="fa fa-home pl-2"></i></Link>
                </li>
                <li className="nav-item categories_sm text-center admin-nav-icon">
                    <Link to='/admin/category' className="nav-link  "><i className="fa fa-list pl-2"></i></Link>
                </li>
                <li className="nav-item sub_sm text-center admin-nav-icon">
                    <Link to='/admin/sub' className="nav-link  "><i className="fa fa-random pl-2"></i></Link>
                </li>
                <li className="nav-item product_sm text-center admin-nav-icon">
                    <Link to='/admin/product' className="nav-link  "><i className="fa fa-shopping-cart pl-2"></i></Link>
                </li>
                <li className="nav-item blogs_sm admin-nav-icon">
                    <Link to='/admin/blog' className="nav-link "><i className="fa fa-book pl-3"></i></Link>
                </li>
                <li className="nav-item coupon_sm admin-nav-icon">
                    <Link to='/admin/coupon' className="nav-link "><i className="fa fa-ticket pl-3"></i></Link>
                </li>
                <li className="nav-item dashboard_sm admin-nav-icon">
                    <Link to='/user/dashboard' className="nav-link "><i className="fa fa-user pl-3"></i></Link>
                </li>
            </ul>

            <ul className="navbar-nav admin_nav_lg flex-column px-0 dash d-lg-flex d-none fixed" onClick={handleAdminNav}>
                <li className="nav-item dashboard">
                    <Link to='/admin/dashboard' className="nav-link"><i className="fa fa-home pr-2"></i>DashBoard</Link>
                </li>
                <li className="nav-item categories">
                    <Link to='/admin/category' className="nav-link"><i className="fa fa-list pr-2"></i>Categories</Link>
                </li>
                <li className="nav-item sub">
                    <Link to='/admin/sub' className="nav-link"><i className="fa fa-random pr-2"></i>Sub Categories</Link>
                </li>
                <li className="nav-item product">
                    <Link to='/admin/product' className="nav-link"><i className="fa fa-shopping-cart pr-2"></i>Product</Link>
                </li>
                <li className="nav-item blogs">
                    <Link to='/admin/blog' className="nav-link"><i className="fa fa-book pr-2"></i>Blog</Link>
                </li>
                <li className="nav-item coupon">
                    <Link to='/admin/coupon' className="nav-link"><i className="fa fa-ticket pr-2"></i>Coupon</Link>
                </li>
                <li className="nav-item dashboard">
                    <Link to='/user/dashboard' className="nav-link"><i className="fa fa-user pr-2"></i>Profile</Link>
                </li>
            </ul>
        </div>
        </>
    )
}

export default AdminNav