import React,{useEffect} from 'react'
import {Link, useHistory} from 'react-router-dom'
import {useSelector, useDispatch} from 'react-redux'
import {Badge} from 'antd'
import './MobNav.css'

const MobNav=({logout, setIsModalVisible})=>{
    const {user, cart, NAV, navMob} = useSelector((state)=>({...state}))
    const dispatch = useDispatch()
    const history = useHistory()

    useEffect(()=>{
        let main = document.querySelectorAll('.upper_mob_nav_item a')
        for(let i=0; i<main.length;i++){
            main[i].classList.remove('nav-mob-active')
        }

        let item = document.querySelector(`.${navMob}`)
        item.classList.add('nav-mob-active')
    }, [NAV, navMob])

    const handleNavClick=(e)=>{
        let page = e.target.className
        dispatch({
            type:'CHANGE_MOB_NAV',
            payload:page
        })

        dispatch({
            type:'CHANGE_NAV',
            payload:page.split('_')[0]
        })
    }
    return(
        <>
        <div id="mySidenav" class="sidenav">
            <div className="user_side">
            {user && user.token ? (
                <>
                    <p className="side-user" onClick={()=>history.push('/user/dashboard')}><i className="fa fa-user pr-3"></i> {user.email.split('@')[0]}</p>          
                </>) : (
                <>				

                    <button className="side-login mt-2" onClick={()=>setIsModalVisible(true)}>Create New Account</button>
                </>
            )}
            </div>
            <hr/>
            <div className="upper_mob_nav_item" onClick={handleNavClick}>
                <Link to='/' className="home_sm"><i className="fa fa-home pr-3"></i>Home</Link>
                <Link to='/shop' className="shop_sm"><i className="fa fa-shopping-bag pr-3"></i>Shop</Link>
                <Link to='/blog' className="blog_sm"><i className="fa fa-book pr-3"></i>Blog</Link>
                <Link to='/contact' className="contact_sm"><i className="fa fa-address-book pr-3"></i>Contact</Link>
                <Link to="/cart" className="cart_sm"><i className="fa fa-shopping-cart pr-3"></i>Cart <Badge count={cart && cart.length}></Badge></Link>
           </div>
           <hr/>
           {user && user.token ? (
               <>
               <Link to={`/${user.role == "admin" ? "admin" : "user"}/dashboard`}><i className="fa fa-area-chart pr-3"></i>DashBoard</Link>
               <Link to="/user/order"><i className="fa fa-inbox pr-3"></i>Order</Link>
               <Link to="/wishlist"><i className="fa fa-heart pr-3"></i>Wishlist</Link>
               <hr/>
                <p onClick={logout} className="side-log"><i className="fa fa-sign-out pr-3"></i> Logout</p>
                <hr/>
               </>
           ) : (
               <>
                <button className="side-login" onClick={()=>history.push('/login')}>Sign In</button>
                <hr/>
               </>
           )}
        </div>
        </>
    )
}

export default MobNav