import React from 'react'
import 'react-quill/dist/quill.snow.css'; // ES6
import ReactQuill from 'react-quill'; // ES6

const ProductDescription = ({description, handleDescriptionChange, modal})=>{
    return(
        <>
            <div>
                <ReactQuill placeholder="Description about your" theme="snow"  style={{height:'150px'}} value={description || ' '} onChange={handleDescriptionChange} className={`${modal ? 'col-11' : 'col-10 col-lg-6'} mb-5 p-0 mx-auto`}></ReactQuill><br/>
            </div>
        </>
    )
}

export default ProductDescription