import { Button, Input, Card, Modal,Tooltip } from 'antd'
import React, {useState, useEffect} from 'react'
import AdminNav from '../../components/nav/AdminNav'
import { createCoupon, listCoupons, removeCoupon } from '../../functions/coupon'
import {useSelector} from 'react-redux'
import {DeleteOutlined, EditOutlined, ReloadOutlined, PlusCircleOutlined,CloseCircleOutlined, CheckCircleOutlined} from '@ant-design/icons'
import {Link} from 'react-router-dom'
import { toast } from 'react-toastify'

const Coupon = ()=>{
    const [isModalVisible, setIsModalVisible] = useState(false)
    const [coupon, setCoupon] = useState([])
    const [title, setTitle] = useState('') 
    const [discountRate, setDiscountRate] = useState(0)
    const [expiry, setExpiry] = useState('')
    const {user} = useSelector(state => ({...state}))

    useEffect(()=>{
        loadAllCoupon()
    },[user])

    const loadAllCoupon = ()=>{
        listCoupons(user && user.token).then((res)=>{
            setCoupon(res.data)
        }).catch((err)=>{
            console.log(err)
        })
    }
    const handleSubmit=()=>{
        createCoupon(user.token, {title, discountRate, expiry}).then((res)=>{
            setTitle('')
            setDiscountRate(0)
            setExpiry('')
            loadAllCoupon(user && user.token)
            toast.success(`${res.data.title} is successfully created!!`)
        }).catch((err)=>{
            toast.error(`${title} is not created...Try Again!!`)
        })
    }

    const handleRemove = (slug)=>{
        removeCoupon(user.token, slug).then((res)=>{
            if(res.data.success){
                toast.success(`${slug} is successfully deleted!!`)
                loadAllCoupon()
            }
        }).catch((err)=>{
            toast.error(`${slug} is not deleted...Try Again!!`)
        })
    }

    const checkValidity = (expiry)=>{
        let todayDate = String(new Date()).split(' ')
        let validTill = String(new Date(expiry)).split(' ')

        if(todayDate[1] === validTill[1] && todayDate[3] === validTill[3]){
            if(todayDate[2] <= validTill[2]){
                return(
                    <CheckCircleOutlined className="text-success"/>
                )
            }else{
                return(
                    <CloseCircleOutlined className="text-danger"/>
                )
            }
        }else{
            return(
                <CloseCircleOutlined className="text-danger"/>
            )
        }
    }
    return(
        <>
        <div className="container-fluid">
            <div className="row">
                <AdminNav/>
                <div className="col-10 pl-2 pr-1 py-5 bg-gray  dash-content">
                    <Card className="card_table">
                    <div className="table-heading d-flex align-items-center justify-content-between">
                        <p className="pt-3 pl-3 text-white">COUPONS</p>
                        <div className=" d-flex  align-items-center justify-content-end">
                        <Tooltip title='Create New Product'><PlusCircleOutlined className=" mr-2 plus_icon" onClick={()=>setIsModalVisible(true)}/></Tooltip>
                        <Tooltip title="Refresh"><ReloadOutlined className="mr-2 plus_icon" onClick={()=>loadAllCoupon()}/></Tooltip>
                        </div>
                    </div>
                    
                    <table className="table table-responsive-sm">
                        <thead className="thead-table">
                            <tr>
                                <th scope="col">Title</th>
                                <th scope="col">Discount Rate</th>
                                <th scope="col">Expiry Date</th>
                                <th scope="col">Valid</th>
                                <th scope="col">Actions</th>
                            </tr>
                        </thead>
                        <tbody>
                            {coupon && coupon.map((item)=>{
                                let {expiry,title,discountRate, slug} = item
                                return(
                                    <>
                                        <tr className="table-body-row">
                                            <td >{title}</td>
                                            <td>{discountRate} %</td>
                                            <td>{expiry.split('T')[0]}</td>
                                            <td>{checkValidity(expiry)}</td>
                                            <td><Tooltip title={'Delete'}><DeleteOutlined className="text-danger pr-2" onClick={()=>handleRemove(slug)}/></Tooltip> 
                                                <Tooltip title={"Edit"}><Link to={`/admin/update/coupon/${slug}`}><EditOutlined className="text-warning"/></Link></Tooltip></td>
                                        </tr>
                                    </>
                                )
                            })}
                        </tbody>
                    </table>
                    </Card>
                </div>

                <Modal title="Create Coupon" visible={isModalVisible} centered onCancel={()=>setIsModalVisible(false)} footer={null}>
                    <Input type="text" placeholder="Category Name" value={title} onChange={(e)=>setTitle(e.target.value)} className=" mb-3"/><br/>
                    <Input type="number" placeholder="Discount Rate" value={discountRate} onChange={(e)=>setDiscountRate(e.target.value)} className=" mb-3"/><br/>
                    <Input type="date" placeholder="Expiry Date" value={expiry} onChange={(e)=>setExpiry(e.target.value)} className=""/><br/>
                    <Button type="primary" shape="round" block className=" my-3" onClick={handleSubmit}>Submit</Button>
                </Modal>
            </div>  
        </div>
        </>
    )
}
export default Coupon