import React, {useEffect} from 'react'
import {Tabs} from 'antd'
import './ProductInfoTab.css'
import StarRatings from 'react-star-ratings'

const ProductInfoTab=({description, review, children})=>{
    const {TabPane} = Tabs

    useEffect(()=>{
        let product_desc = document.querySelector('.product_desc')
        product_desc.innerHTML = description
    }, [description])
    return(
        <>
        <Tabs defaultActiveKey="desc">
            <TabPane key="desc" tab="Description">
                <p className="product_desc"></p>
            </TabPane>
            <TabPane key="review" tab="Review">
            {review && review.map((item)=>{
                            return(
                                <>
                                    <div>
                                        <StarRatings numberOfStar = {5} starRatedColor="red" starSpacing="2px" starDimension="16px" rating={item.star}></StarRatings>
                                        <p className="pt-2 author_cmnt_name">{item.postedBy.address.firstName} {item.postedBy.address.lastName && item.postedBy.address.lastName}</p>
                                        <p className="author_cmnt">{item.comment}</p>
                                        <hr/>
                                    </div>
                                </>
                            )
                        })}
                        {children}
            </TabPane>
        </Tabs>
        </>
    )
}

export default ProductInfoTab