export const navReducer = (state = 'home', action)=>{
    switch(action.type){
        case 'CHANGE_NAV':
            return action.payload;
        default:
            return state
    }
}

export const navMobReducer = (state = 'home_sm', action)=>{
    switch(action.type){
        case 'CHANGE_MOB_NAV':
            return action.payload
        default:
            return state
    }
}