export const adminNav = (state = 'dashboard', action)=>{
    switch(action.type){
        case 'CHANGE_ADMIN_NAV':
            return action.payload
        default:
            return state
    }
}

export const adminNavMob = (state = 'dashboard_sm', action)=>{
    switch(action.type){
        case 'CHANGE_ADMIN_MOB_NAV':
            return action.payload
        default:
            return state
    }
}