import React, {useEffect} from 'react'
import {Route,Switch, useHistory} from 'react-router-dom'
import {auth} from './firebase'
import {useDispatch, useSelector} from 'react-redux'
import {createOrUpdateUser} from './functions/auth.js'
import {ToastContainer} from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css';

import Home from './pages/universal/Home'
import Blog from './pages/universal/BlogPage'
import BlogInfo from './pages/universal/BlogInfo'
import ContactUs from './pages/universal/ContactUs'
import Shop from './pages/universal/Shop'
import Cart from './pages/universal/Cart'
import ProductInfo from './pages/universal/ProductInfo'
import WishList from './pages/universal/Wishlist'
import Address from './pages/universal/Address'
import CheckOut from './pages/universal/CheckOut'

import Login from './pages/auth/Login'
import DashBoard from './pages/admin/Dashboard'
import Category from './pages/admin/Category'
import Sub from './pages/admin/Sub'
import Blogs from './pages/admin/Blogs'
import Product from './pages/admin/Products'
import Coupon from './pages/admin/Coupon'
import UpdateCategory from './pages/admin/UpdateCategory'
import UpdateSub from './pages/admin/UpdateSub'
import UpdateProduct from './pages/admin/UpdateProduct'
import UpdateBlog from './pages/admin/UpdateBlog'
import UpdateCoupon from './pages/admin/UpdateCoupon'

import Profile from './pages/user/Profile'
import UserOrder from './pages/user/UserOrder'
import UserOrderInfo from './pages/user/UserOrderInfo'
import Navbar from './components/nav/Navbar'
import SideDrawer from './components/SideDrawer'
import Bot from './components/home/Bot'
import OrderPlaced from './pages/universal/OrderPlaced'

const App = ()=>{
	const dispatch = useDispatch()
	const {user} = useSelector((state)=>({...state}))
	const history = useHistory()

	useEffect(async()=>{
		await auth.onAuthStateChanged(async(user)=>{
			if(user){
				const idToken = await user.getIdTokenResult()
				let user_token = idToken.token
				createOrUpdateUser(user_token).then((res)=>{
					let {email,role, _id, newsletter} = res.data
					dispatch({
						type:'LOGGED_IN_USER',
						payload:{
							email,
							role,
							_id,
							newsletter,
							token:user_token
						}
					})
					history.push('/')
				}).catch((err)=>{
					console.log(err.message)
				})
			}
		})
	}, [])

	const renderAdminRoutes = ()=>{
		if(user && user.role === 'admin'){
			return(
				<>
					<Route exact path='/admin/dashboard' component={DashBoard}></Route>
					<Route exact path='/admin/category' component={Category}></Route>
					<Route exact path='/admin/sub' component={Sub}></Route>
					<Route exact path='/admin/product' component={Product}></Route>
					<Route exact path='/admin/blog' component={Blogs}></Route>
					<Route exact path='/admin/coupon' component={Coupon}></Route>
					<Route exact path='/admin/update/category/:slug' component={UpdateCategory}></Route>
					<Route exact path='/admin/update/sub/:slug' component={UpdateSub}></Route>
					<Route exact path='/admin/update/product/:slug' component={UpdateProduct}></Route>
					<Route exact path='/admin/update/blog/:slug' component={UpdateBlog}></Route>
					<Route exact path='/admin/update/coupon/:slug' component={UpdateCoupon}></Route>
				</>
			)
		}
	}

	const renderRoutes = ()=>{
		if(user){
			return(
				<>
					<Route exact path='/wishlist' component={WishList}></Route>
					<Route exact path='/user/order' component={UserOrder}></Route>
					<Route exact path='/user/order/:id' component={UserOrderInfo}></Route>
					<Route exact path='/address' component={Address}></Route>
					<Route exact path='/checkout' component={CheckOut}></Route>
					<Route exact path='/user/dashboard' component={Profile}></Route>
					<Route exact path='/order/:orderId/placed' component={OrderPlaced}></Route>
					{renderAdminRoutes()}
				</>
			)
		}else if(!user){
			return(
				<>
					<Route exact path='/login' component={Login}></Route>
				</>
			)
		}
	}
	return(
		<>
		<Navbar/>
		<SideDrawer/>
		<ToastContainer/>
		<Bot/>
		<Switch>
			<Route exact path='/' component={Home}></Route>
			<Route exact path='/blog' component={Blog}></Route>
			<Route exact path='/blog/:slug' component={BlogInfo}></Route>
			<Route exact path='/shop' component={Shop}></Route>
			<Route exact path='/contact' component={ContactUs}></Route>
			<Route exact path='/cart' component={Cart}></Route>
			<Route exact path='/product/:slug' component={ProductInfo}></Route>
			{renderRoutes()}
		</Switch>
		</>
	)
}

export default App